.is-hidden {
  display: none;
}

body {
  background-color: rgba(191, 226, 231, .49);
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.search-form {
  width: 100%;
  background: linear-gradient(to right, #191654, #43c6ac);
  justify-content: center;
  align-items: center;
  padding: 25px 0;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 3px 5px rgba(0, 0, 0, .5);
}

.search-form__input {
  width: 519px;
  height: 28px;
  border: none;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  outline: none;
  padding: 10px;
  font-size: 18px;
}

.search-form__button {
  width: 100px;
  height: 48px;
  cursor: pointer;
  background-color: rgba(237, 241, 242, .8);
  border: none;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 16px;
}

.search-form__button:hover, .search-form__button:focus {
  color: #fff;
  background-color: #1f256e;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  padding-top: 110px;
  display: flex;
}

.gallery-item {
  margin-bottom: 20px;
  overflow: hidden;
}

.photo-card {
  width: 300px;
  height: 100%;
  border-radius: 5px;
  flex-direction: column;
  margin-bottom: 20px;
  padding: 7px;
  display: flex;
  box-shadow: 0 3px 8px rgba(0, 0, 0, .5);
}

.photo-card:hover, .photo-card:focus {
  border: 1px solid #f9f9f9;
  box-shadow: 0 1px 14px #02086b;
}

.info {
  justify-content: space-between;
  display: flex;
}

.info-item {
  text-align: center;
  color: rgba(57, 60, 186, .67);
  flex-direction: column;
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 16px;
  font-weight: 700;
  display: flex;
}

.load-more {
  width: 200px;
  height: 40px;
  background-color: rgba(9, 167, 150, .66);
  border: none;
  border-radius: 5px;
  margin-top: 50px;
  margin-bottom: 30px;
  margin-left: 45%;
  font-size: 16px;
}

.load-more:hover, .load-more:focus {
  color: #fff;
  background-color: #1f256e;
}

